




























import {SequenceManager} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component
export default class PALGlobalSequenceView extends Vue {
  data: SequenceManager[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getSmList(null).then((res) => {
      this.data = res;
    });
  }
}
