

























import {initialDeviceManagement} from "@/core/constants/api/initialDeviceManagement";
import {DeviceManagement} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component
export default class EditDeviceModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop(Number) readonly id!: number;

  formData: DeviceManagement = {...initialDeviceManagement()};

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.getEditDevice();
    } else {
      this.formData = {...initialDeviceManagement()};
    }
  }

  getEditDevice(): void {
    this.api.getDeviceManagement(this.id).then((res) => {
      this.formData = res;
    });
  }

  editDeviceManagement(): void {
    const params = this._.pick(this.formData, ["ch_num", "name", "note", "serial_num"]);

    this.api.editDeviceManagement(params, this.id).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {...initialDeviceManagement()};
    this.dismiss();
  }
}
