































































































































import AddEventModal from "@/components/events/AddEventModal.vue";
import {initialSm} from "@/core/constants/api/initialSm";
import {GlobalTag, SequenceManager} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";
@Component({
  components: {AddEventModal},
})
export default class AddSequenceModal extends Mixins(AsModal) {
  displayAddEventModal: boolean = false;
  @Emit("refresh") refresh(): void {}

  formData: SequenceManager = {...initialSm()};
  tags: number[] = [];
  tagName: string = "";

  tagList: GlobalTag[] = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getTagList(null).then((res) => {
        this.tagList = res;
      });
    }
  }

  addTag(): void {
    this.api.addTag({name: this.tagName}).then((res) => {
      this.api.getTagList(null).then((tagList) => {
        this.tagList = tagList;
        this.tags = [...this.tags, res.id];
        this.tagName = null;
      });
    });
  }

  submit(): void {
    this.formData.tags = this.tags.join(",");
    this.api.addSm(this.formData).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {...initialSm()};
    this.tags = [];
    this.tagName = "";
    this.dismiss();
  }
  mockData = [
    {
      taskName: "TASK 1",
      eventName: "3",
      status: "Ready",
      createDate: "2019-08-19",
      round: 3,
      events: [
        {name: "Event 1", status: "Ready", type: "Measurement", startTime: "-", action: "Download Audio"},
        {name: "Event 2", status: "Ready", type: "Pause", startTime: "-", action: ""},
        {name: "Event 3", status: "Ready", type: "Pause", startTime: "-", action: ""},
        {name: "Event 4", status: "Ready", type: "Pause", startTime: "-", action: ""},
      ],
    },
    {
      taskName: "TASK 2",
      eventName: "5",
      status: "Running",
      createDate: "2019-08-19",
      round: 1,
      events: [
        {name: "Event 1", status: "Ready", type: "Measurement", startTime: "-", action: "Download Audio"},
        {name: "Event 2", status: "Ready", type: "Pause", startTime: "-", action: ""},
        {name: "Event 3", status: "Ready", type: "Pause", startTime: "-", action: ""},
        {name: "Event 4", status: "Ready", type: "Pause", startTime: "-", action: ""},
      ],
    },
  ];
}
