



































































import AddLabModal from "@/components/user-account-management/AddLabModal.vue";
import EditLabModal from "@/components/user-account-management/EditLabModal.vue";
import {LabSerializerWithPopulate} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";
@Component({
  components: {EditLabModal, AddLabModal},
})
export default class EditAdminAccountModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop(Number) readonly id!: number;
  @Prop(String) readonly name!: string;
  @Prop(Array) readonly labs!: number[];

  displayAddLabModal: boolean = false;
  displayEditLabModal: boolean = false;
  editName: string = "";
  editId: number = 0;
  editLabs: number[] = [];
  labList: LabSerializerWithPopulate[] = [];
  selectedLab: LabSerializerWithPopulate = null;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.editName = this.name;
      this.editId = this.id;
      this.editLabs = this.labs;
      this.getData();
    } else {
      this.editName = "";
      this.editId = 0;
      this.editLabs = [];
    }
  }

  submit(): void {
    this.api.patchAccountName(this.editName, this.editId).then(() => {
      this.refresh();
      this.clean();
    });
  }

  getData(): void {
    const ids = this.editLabs.join(",");
    this.api.getLabList({ids}).then((res) => {
      this.labList = res;
    });
  }

  clean(): void {
    this.dismiss();
  }
  editLab(lab: LabSerializerWithPopulate): void {
    this.selectedLab = lab;
    this.displayEditLabModal = true;
  }

  addLab(id: number) {
    const ids = [
      ...this.labList.map((lab) => {
        return lab.id;
      }),
      id,
    ].join(",");
    this.api.getLabList({ids}).then((res) => {
      this.labList = res;
    });
  }
}
