

















































import {initialTdb} from "@/core/constants/api/initialTdb";
import AsModal from "@/mixins/AsModal";
import {TransducerDB} from "@/types/ApiTypes";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class AddTransducerModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData: TransducerDB = {...initialTdb()};

  add(): void {
    this.api.addTransducerDB(this.formData).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {...initialTdb()};
    this.dismiss();
  }
}
