





































































































































































import AddLabModal from "@/components/user-account-management/AddLabModal.vue";
import EditLabModal from "@/components/user-account-management/EditLabModal.vue";
import {Account, LabSerializerWithPopulate, PlanManagement} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component({
  components: {EditLabModal, AddLabModal},
})
export default class EditUserAccountModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop(Object) readonly acc!: Account;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.acc.labs = this.acc.labs.map((lab) => {
        return lab.id as any;
      });
      this.getData();
    }
  }

  planList: PlanManagement[] = [];
  labList: LabSerializerWithPopulate[] = [];
  selectedLab: LabSerializerWithPopulate = null;

  addLab(id: number) {
    const ids = [
      ...this.labList.map((lab) => {
        return lab.id;
      }),
      id,
    ].join(",");
    this.api.getLabList({ids}).then((res) => {
      this.labList = res;
    });
  }

  editAcc(): void {
    const labs = this.labList.map((lab) => {
      return lab.id;
    });
    const params = {...this._.omit(this.acc, ["password", "labs"]), labs} as Account;
    this.api.patchUserAccount(params, this.acc.id).then(() => {
      this.clean();
    });
  }

  getData(): void {
    const ids = this.acc.labs.join(",");
    this.api.getLabList({ids}).then((res) => {
      this.labList = res;
    });
    this.api.getPlanList({fields: "id,plan_name"}).then((res) => {
      this.planList = res;
      this.acc.plan_management = (this.acc.plan_management as PlanManagement).id;
    });
  }

  clean(): void {
    this.planList = [];
    this.labList = [];
    this.dismiss();
    this.refresh();
  }

  cancel(): void {
    // const newLabs = this.labList.map((lab) => {
    //   return lab.id;
    // });
    // const removeIds = this._.difference(newLabs, this.acc.labs as number[]);
    // removeIds.forEach((id) => {
    //   this.api.deleteLab(id as any).catch(console.error);
    // });
    this.clean();
  }

  displayAddLabModal: boolean = false;
  displayEditLabModal: boolean = false;
  editLab(lab: LabSerializerWithPopulate): void {
    this.selectedLab = lab;
    this.displayEditLabModal = true;
  }
}
